.loader {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;    
}

.cargando {
    color: white;
    font-size: 52px;
    text-transform: uppercase;
    font-weight: 700;
}